$cache: 2;
@font-face {
  font-family: 'euro-icomoon';
  src:  url('../fonts/euro-icomoon.eot?#{$cache}');
  src:  url('../fonts/euro-icomoon.eot?#{$cache}') format('embedded-opentype'),
    url('../fonts/euro-icomoon.ttf?#{$cache}') format('truetype'),
    url('../fonts/euro-icomoon.woff?#{$cache}') format('woff'),
    url('../fonts/euro-icomoon.svg?#{$cache}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="euro-"], [class*=" euro-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'euro-icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

